"use client";

import React, { useState, useEffect } from "react";
import { useParams } from "next/navigation";
import { ThemeProvider, useTheme } from "@/lib/theme-context";
import { PhysicsHero } from "@/components/PhysicsHero";
import { SettingsPanel } from "@/components/SettingsPanel";
import { BookingModal } from "@/components/BookingModal";
import { ScheduleViewWrapper } from "@/components/ScheduleViewWrapper";
import { useLineLiff } from "@/hooks/use-line-liff";
import { fetchStudio } from "@/actions/studio";
import Link from "next/link";
import { Button } from "@/components/ui/button";

// Wrapper to provide ID context
export default function StudioPageWrapper() {
    return (
        <ThemeProvider>
            <StudioPageContent />
        </ThemeProvider>
    );
}

function StudioPageContent() {
    const params = useParams();
    const id = params.id as string;

    const [isBookingOpen, setIsBookingOpen] = useState(false);
    const { profile } = useLineLiff();
    const { setLogoUrl, setBackgroundImage } = useTheme(); // Use theme context to update visuals dynamically

    const [studio, setStudio] = useState<any>(null);

    useEffect(() => {
        const load = async () => {
            if (!id) return;
            const data = await fetchStudio(id);
            if (data) {
                setStudio(data);
                // In real app, studio data might have theme colors or logo urls.
                // For now, let's pretend specific studios might have these or use storeName as logo text alternative
                // setLogoUrl(data.logoUrl); 
            }
        };
        load();
    }, [id, setLogoUrl, setBackgroundImage]);

    if (!studio) return <div className="min-h-screen bg-black flex items-center justify-center text-white">Loading Studio...</div>;

    return (
        <main className="min-h-screen bg-black text-white selection:bg-cyan-500 selection:text-black">
            {/* Header / Nav */}
            <header className="absolute top-0 w-full p-6 flex justify-between items-center z-50 pointer-events-none px-10">
                <div className="text-sm font-mono tracking-widest opacity-50 uppercase">
                    {studio.storeName} // V1.0
                </div>
                <div className="flex gap-4 items-center pointer-events-auto">
                    <ScheduleViewWrapper />
                    <div className="text-sm font-mono tracking-widest opacity-50">
                        {profile ? `Player: ${profile.displayName}` : "Guest Mode"}
                    </div>
                </div>
            </header>

            {/* Physics Hero Area - Pass Studio Name or Context */}
            <div className="relative">
                <PhysicsHero onBookingTrigger={() => setIsBookingOpen(true)} />
                {/* Subtitle location indicator */}
                <div className="absolute top-[160px] left-10 pointer-events-none opacity-50 text-white font-mono text-xs md:text-sm">
                    LOC: {studio.address}
                </div>
            </div>

            {/* Settings */}
            <SettingsPanel />

            {/* Booking Modal - Could inject Studio specific rooms here later */}
            <BookingModal isOpen={isBookingOpen} onOpenChange={setIsBookingOpen} studioData={studio} />

            {/* Content Section below fold */}
            <section className="container mx-auto px-4 py-20 space-y-20">
                <div className="text-center mb-10">
                    <h2 className="text-3xl font-bold mb-4">REHEARSAL ROOMS</h2>
                    <p className="text-gray-400">このスタジオで利用可能な防音室</p>
                </div>

                <div className="grid md:grid-cols-2 lg:grid-cols-3 gap-8">
                    {studio.rooms?.length > 0 ? studio.rooms.map((room: any, i: number) => (
                        <div key={i} className="p-6 border border-white/10 rounded-lg bg-slate-900/50 hover:border-cyan-500 hover:shadow-[0_0_20px_rgba(6,182,212,0.2)] transition-all">
                            <Link href={`/studios/${id}/rooms/${i}`} className="block">
                                <h3 className="text-2xl font-bold mb-2 text-cyan-400 flex justify-between items-center">
                                    {room.name}
                                    <span className="text-xs bg-cyan-950 px-2 py-1 rounded text-cyan-200">SCHEDULE &gt;</span>
                                </h3>
                            </Link>
                            <div className="space-y-2 text-sm text-gray-400 font-mono mt-4">
                                <div className="flex justify-between border-b border-white/5 pb-1">
                                    <span>平日</span>
                                    <span>10:00~: ¥{room.pricing?.weekday?.slots?.[0]?.price || 0}/h</span>
                                </div>
                                <div className="flex justify-between border-b border-white/5 pb-1">
                                    <span>土曜</span>
                                    <span>10:00~: ¥{room.pricing?.saturday?.slots?.[0]?.price || 0}/h</span>
                                </div>
                                <div className="flex justify-between border-b border-white/5 pb-1">
                                    <span>日祝</span>
                                    <span>10:00~: ¥{room.pricing?.sundayHoliday?.slots?.[0]?.price || 0}/h</span>
                                </div>
                            </div>

                            <div className="mt-4 pt-4 border-t border-white/10">
                                <Link href={`/studios/${id}/rooms/${i}`}>
                                    <Button className="w-full bg-cyan-900/40 text-cyan-400 hover:bg-cyan-500 hover:text-black">
                                        予約カレンダーを見る
                                    </Button>
                                </Link>
                            </div>
                        </div>
                    )) : (
                        <div className="col-span-full text-center text-gray-500">ルーム情報なし</div>
                    )}
                </div>
            </section>

            <footer className="border-t border-white/10 py-10 text-center text-gray-500 text-sm">
                © 2026 {studio.storeName}
            </footer>
        </main>
    );
}
